<template>
  <div class="profile-steps">
    <div
      v-for="(step, key) in steps"
      :key="step.name"
      :class="`profile-steps__step ${getStepClass(step.id)}`"
    >
      <div class="profile-steps__step--count">{{ key + 1 }}</div>
      <div class="inline">
        <div class="profile-steps__step--name"
        :style="getStyle(step)">{{ step.name }}</div>
        <div v-if="showPrompt(step.id)" class="profile-steps__step--prompt">
          {{ step.prompt }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    currentStep: {
      type: Number,
      required: false,
    },
  },
  data() {
    return {
      steps: [
        {
          id: 0,
          name: 'Заполнение заявки',
          color: '#d88ca8',
          prompt: 'Поздравляем! Вы успешно прошли этап регистрации',
        },
        {
          id: 1,
          name: 'На рассмотрении',
          prompt: 'Ваша заявка на конкурс проверяется экспертами',
          color: '#0094B3'
        },
        {
          id: 2,
          name: 'Участник очного этапа',
          prompt: 'Поздравляем! Вы перешли на очный этап конкурса',
          color: '#6E2C6B'
        },
        {
          id: 3,
          name: 'Номинант',
          prompt: 'Поздравляем! Вы перешли на очный этап конкурса',
          color: '#E62D3B'
        },
        {
          id: 4,
          name: 'Лауреат',
          prompt: 'Поздравляем! Вы перешли на очный этап конкурса',
          color: '#FCD800 '
        },
        {
          id: 5,
          name: 'Победитель',
          prompt: 'Поздравляем! Вы перешли на очный этап конкурса',
          color: '#459D27 '
        },
      ],
    }
  },
  methods: {
    getStepClass(id) {
      if (id < this.currentStep) return 'profile-steps__step--award'
      if (id === this.currentStep) return 'profile-steps__step--current'
      if (id > this.currentStep) return 'profile-steps__step--not-award'
    },
    showPrompt(id) {
      return id <= this.currentStep
    },
    getStyle(step) {
      return {
        background: this.showPrompt(step.id) ? step.color : 'transparent',
      }
    }
  }
}
</script>

